import { mailbox } from "./connection";
import { handleFeedback, initialState } from "./provider";
import { produce } from 'immer'
// @refresh reset

/**
 * Although most of the data transfer are JSON based dictionary
 * Some, for optimization, sent as number[], which is very compact, around 20 bytes
 *
 */

export class FlowmeterUpdateEvent {
  flowmeter: number[];
  flowrate: number[];
  constructor(data: number[]) {
    this.flowmeter = [data[0], data[2], data[4]];
    this.flowrate = [data[1], data[3], data[5]];
  }
}

export class ButtonClickEvent {
  groupId: number;
  controlButton: boolean;
  coffeeButtons: boolean[]; // 4 buttons
  doseButton: boolean;

  // if groupId == 0, use these
  _waterButton: boolean;
  _doseButton: boolean;
  _warmerButton: boolean;

  constructor(data: number[]) {
    this.groupId = data[0] as number;
    this.controlButton = !data[1];
    this.coffeeButtons = [!data[2], !data[3], !data[4], !data[5]];
    this.doseButton = !data[6];
    // this._waterButton = !data[7];
    // this._doseButton = !data[8];
    // this._warmerButton = !data[9];
  }
}

export class ButtonBrightnessEvent {
  groupId: number;
  controlButton: number;
  coffeeButtons: number[]; // 4 buttons
  doseButton: number;
  // if groupId == 0, use these
  _waterButton: number;
  _doseButton: number;
  _warmerButton: number;

  constructor(data: number[]) {
    this.groupId = data[0] as number;
    this.controlButton = data[1];
    this.coffeeButtons = [data[2], data[3], data[4], data[5]];
    this.doseButton = data[6];
    // this._waterButton = data[7];
    // this._doseButton = data[8];
    // this._warmerButton = data[9];
  }
}

export class RelayUpdateEvent {
  pump: number; // 0
  group: boolean[]; // 1, 2, 3
  refill: boolean; // 4
  warmer: boolean; // 5
  dose: boolean; // 6
  boiler: boolean; // 7

  constructor(data: number[]) {
    this.pump = data[0];
    this.group = [!!data[1], !!data[2], !!data[3]];
    this.refill = !!data[4];
    this.warmer = !!data[5];
    this.dose = !!data[6];
    this.boiler = !!data[7];
  }
}

export class PidUpdateEvent {
  kp: number[];
  ki: number[];
  kd: number[];
  taret: number[];
  range: number[];
  temperature: number[];
  power: number[];

  constructor(data: number[]) {}
}

export class TerminalUpdateEvent {
  message: string;
  constructor(data: string) {
    this.message = data;
  }
}

export type UpdateEvent =
  | ButtonClickEvent
  | ButtonBrightnessEvent
  | RelayUpdateEvent
  | PidUpdateEvent
  | TerminalUpdateEvent
  | FlowmeterUpdateEvent;

export class UnknownUpdateEvent {
  data: number[];
  constructor(data: number[]) {
    this.data = data;
  }
}

interface ResponseQueryEvent {}
enum EventType {
  ButtonClick = 0xb0,
  RelayUpdate = 0xb1,
  FlowmeterUpdate = 0xb2,
  PidUpdate = 0xb3,
  ButtonBrightness = 0xb5,
  TerminalUpdate = 0xb6,
  ResponseQuery = 0xb7,
}

import { Header } from "./connection";
export function interpretMessage(
  header: Header,
  data: number[] | { [key: string]: any },
) {
  console.debug("Interpreet", data);

  const ctx = (window as any).ctx as typeof initialState;

  if (Array.isArray(data)) {
    console.debug("Data is an array");

    const uuid = data[0];
    const event = data[1];
    const payload = data.slice(2);

    if (event == EventType.ButtonClick) {
      // updateState(new ButtonClickEvent(payload))
    } else if (event == EventType.ButtonBrightness) {
      // updateState(new ButtonBrightnessEvent(payload))
    } else if (event == EventType.RelayUpdate) {
      // updateState(new RelayUpdateEvent(payload))
    } else if (event == EventType.PidUpdate) {
      // updateState(new PidUpdateEvent(payload))
    } else if (event == EventType.TerminalUpdate) {
      // updateState(new TerminalUpdateEvent(payload as any))
    } else if (event == EventType.FlowmeterUpdate) {
      // updateState(new FlowmeterUpdateEvent(payload))
      console.log('fm/ update', event, payload)
      // this is an array of 8
      // 0 is flowcount, 1 is flowrate and so on
      // don't update state too fast


      // ctx.setFlowCount(produce(ctx.flowCount, draft => {
      //   draft = [
      //     payload[0],
      //     payload[2],
      //     payload[4]
      //   ]
      //   return draft
      // }))
      // ctx.setFlowRate(
      //   produce(ctx.flowRate, draft => {
      //     draft = [
      //       payload[1],
      //       payload[3],
      //       payload[5]
      //     ]
      //     return draft
      //   }))

      ctx.setFlowmeterDisplay(produce(ctx.flowmeterDisplay, draft => {
        draft = [
          {
            flowCount: Math.round((payload[0] * ctx.calibration.flowmeter[0]) * 10) / 10,
            flowRate: Math.round((payload[1] * ctx.calibration.flowmeter[0]) * 10) / 10
          },
          {
            flowCount: Math.round((payload[2] * ctx.calibration.flowmeter[1]) * 10) / 10,
            flowRate: Math.round((payload[3] * ctx.calibration.flowmeter[1]) * 10) / 10
          },
          {
            flowCount: Math.round((payload[4] * ctx.calibration.flowmeter[2]) * 10) / 10,
            flowRate: Math.round((payload[5] * ctx.calibration.flowmeter[2]) * 10) / 10
          }
        ]
        return draft
      }))

    } else {
      return new UnknownUpdateEvent(payload);
    }
  } else if (typeof data === "object") {
    console.debug("Data is an object/dictionary");
    const id = data["id"] as number;

    if (mailbox.hasOwnProperty(id)) {
      mailbox[id] = data;
    }

    if (data.command && data.command.startsWith("rp.")) {
      handleFeedback(data);
    }
  }
}
