import {
  ActionClass,
  ActionType,
  GrinderAction,
  ProfilingAction,
  PumpAction,
} from "./action";
import { TargetClass, TargetType, Time, Weight } from "./target";

// Interim object
export type IStepFeedback = {
  //

  stepId: number;
  groupId: number;
  target: {
    type: EnumTarget;
    total: number;
    value: number;
  };
  action: {
    profilingType: EnumProfileType;
    expectedValue: number;
    timestamp: number;
    input: number;
    pressure: number;
    output: number;
  };
};

export enum EnumTarget {
  Time = "Time",
  Weight = "Weight",
}
export enum EnumAction {
  Grind = "Grind",
  Rest = "Rest",
  Extract = "Extract",
  Profile = "Profile",
  PourOver = "Pour",
  Sound = "Sound",
  RGB = "RGB",
}

export type IStep = {
  id: string;
  action: IAction;
  target: ITarget;
};

export type ITarget = {
  targetType: EnumTarget;
  value: number;
  current?: number; // for reporting progress
};

export type IPourOverParam = {
  /* 
        Pour over 
    */
  actionType: "Pour";
  stepAmount: number;
  duration: number;
  pumpPower: number;
};
export type ISoundParam = {
  actionType: "Sound";
};
export type IRGBParam = {
  actionType: "RGB";
  color: string;
  start: number;
  stop: number;
  animation: string;

};

export type IAction =
  | IRestParam
  | IGrindParam
  | IExtractParam
  | IProfileParam
  | IPourOverParam
  | ISoundParam
  | IRGBParam;

export type IRestParam = {
  actionType: "Rest";
  valveState: boolean;
};

export type IGrindParam = {
  actionType: "Grind";
  ingredient: string;
  canSkip: boolean;
  mustWait: boolean;
};

export type IExtractParam = {
  actionType: "Extract";
  power: number;
};

export enum EnumProfileType {
  Pressure = "Pressure",
  Flowrate = "Flowrate",
  Power = "Power",
}

export type IProfileParam = {
  actionType: "Profile";
  segments: IProfilingSegment[];
  profilingType: EnumProfileType;
  updateInterval: number;
};



export type IProfilingSegment = {
  duration: number;
  value: number;
  smooth: boolean;
};

//! Deprecated
export class Step {
  action: ActionClass;
  target: TargetClass;
  constructor({
    action,
    target,
  }: {
    action: ActionClass;
    target: TargetClass;
  }) {
    this.action = action;
    this.target = target;
  }

  toData() {
    return {
      action: this.action.toData(),
      target: this.target.toData(),
    };
  }

  static fromData(data: any) {
    let _action: ActionClass;
    switch (data.actionType) {
      case ActionType.Grinding:
        _action = new GrinderAction(data.action);
        break;
      case ActionType.Extracting:
        _action = new PumpAction(data.action);
        break;
      case ActionType.Profiling:
        _action = new ProfilingAction(data.action);
        break;
      default:
        _action = new GrinderAction(data.action);
    }
    let _target: TargetClass;
    switch (data.targetType) {
      case TargetType.Time:
        _target = new Time(data.target);
        break;
      case TargetType.Weight:
        _target = new Weight(data.target);
        break;
      default:
        _target = new Time(data.target);
    }
    return new Step({
      action: _action,
      target: _target,
    });
  }
}
