import { useEffect, useState, useRef } from 'react'
import demo from "../../public/432997820_1300659664171876_8586090559895363958_n.jpeg"
import useDeviceContext from '@/hooks/useDeviceContext';
import { cn } from '@/util';
import { motion } from 'framer-motion'

const data = [
    {
        name: 'Page A',
        uv: 4000,
        pv: 2400,
        amt: 2400,
    },
    {
        name: 'Page B',
        uv: 3000,
        pv: 1398,
        amt: 2210,
    },
    {
        name: 'Page C',
        uv: 2000,
        pv: 9800,
        amt: 2290,
    },
    {
        name: 'Page D',
        uv: 2780,
        pv: 3908,
        amt: 2000,
    },
    {
        name: 'Page E',
        uv: 1890,
        pv: 4800,
        amt: 2181,
    },
    {
        name: 'Page F',
        uv: 2390,
        pv: 3800,
        amt: 2500,
    },
    {
        name: 'Page G',
        uv: 3490,
        pv: 4300,
        amt: 2100,
    },
];


type MissionPoint = {
    pressure: number
    flowrate: number
    power: number
    weight: number
    time: number
}

const MissionControl = () => {


    const timeout = useRef<ReturnType<typeof setTimeout>>()


    const [data, setData] = useState<MissionPoint[]>([])
    const startTimestamp = useRef<number>(0)

    const { setModalDisplay } = useDeviceContext()


    const [height, setHeight] = useState(0)
    const [width, setWidth] = useState(0)


    const [flip, setFlip] = useState(false)

    const [missionStartTimestamp, setmissionStartTimestamp] = useState(0)
    useEffect(() => {
        window.addEventListener('resize', () => {
            setFlip(window.innerHeight > window.innerWidth)
        })
    })

    useEffect(() => {
        setHeight(window.innerHeight)
        setWidth(window.innerWidth)
    }, [setHeight, setWidth])




    useEffect(() => {

        (window as any).startMissionControl = (e) => {
            setmissionStartTimestamp(Date.now())
            console.log("rp.start mission stasrt")
            setData([])

        }


        (window as any).updateMissionControl = (update: any) => {
            console.log("mission/", update, data)

            const currentState: MissionPoint = {
                pressure: update.pressure < 0.5 ? 0 : Math.round((update.pressure || 0) * 10) / 10,
                flowrate: Math.round((update.flowrate || 0) * 10) / 10,
                power: update.power,
                weight: Math.round((update.flowcount || 0) * 10) / 10,
                time: Math.round((Date.now() - missionStartTimestamp || 0) / 1000)
            }

            setData(produce(data, draft => {
                draft.push(currentState)
            }))


        }

        (window as any).finishMissionControl = () => {
            const currentState: MissionPoint = {
                pressure: 0,
                flowrate: 0,
                power: 0,
                weight: 0,
                time: Math.round((Date.now() - missionStartTimestamp || 0) / 1000)
            }

            // setData(produce(data, draft => {
            //     draft.push(currentState)
            // }))

        }

        // clear the values


    })


    return (
        <motion.div
            className={cn("w-full h-full overflow-clip  flex flex-col")}
            animate={{
                rotate: flip ? 90 : 0
            }}

        >
            <div className="flex flex-row h-20 mt-6 px-20 justify-between items-start">
                <div className="flex flex-col">
                    <h1 className="text-xl text-gray-300">Time</h1>
                    <h1 className="text-2xl text-black">{(Math.round((Date.now() - missionStartTimestamp) / 100) / 10).toFixed(1) || 0} s</h1>
                </div>
                <div className="flex flex-col">
                    <h1 className="text-xl text-gray-300">Weight</h1>
                    <h1 className="text-2xl text-black">{(data[data.length - 1]?.weight || 0).toFixed(1)} gram</h1>
                </div>
                <div className="flex flex-col">
                    <h1 className="text-xl text-gray-300">Flowrate</h1>
                    <h1 className="text-2xl text-black">{(data[data.length -1]?.flowrate || 0).toFixed(1)} gram/s</h1>
                </div>
                <div className="flex flex-col">
                    <h1 className="text-xl text-gray-300">Pressure</h1>
                    <h1 className="text-2xl text-black">{(data[data.length -1]?.pressure || 0).toFixed(1)} bar</h1>
                </div>
                <button
                    className="bg-red-300 hover:bg-red-700 text-white font-bold py-2 px-4 rounded h-16"
                    onClick={() => {
                        setModalDisplay(false)
                    }}
                >
                    Close
                </button>
            </div>

            <div className="w-full h-full">
                <ResponsiveContainer width="100%" height="100%">
                    <ComposedChart data={data}>
                        <CartesianGrid strokeDasharray="3 3" />

                        <XAxis dataKey="time" interval={40} padding={{ left: 30, right: 30 }} />



                        <YAxis width={80} yAxisId="left" tick={{ fontSize: 10 }} domain={[0, 12]}>
                        </YAxis>
                        <YAxis width={80} yAxisId="right" orientation="right" tick={{ fontSize: 10, }} domain={[0, 20]}>
                        </YAxis>
                        <Tooltip />
                        <Legend />

                        {/* <Area isAnimationActive={false} dot={false} type="monotone" dataKey="weight" yAxisId="right" fill="#eeeeee" fillOpacity={0.8} /> */}

                        <Line isAnimationActive={false} dot={false} type="monotone" dataKey="pressure" yAxisId="left" stroke="#8884d8" activeDot={{ r: 8 }} strokeWidth={5} />
                        <Line isAnimationActive={false} dot={false} type="monotone" dataKey="flowrate" yAxisId="right" stroke="#00aeef" strokeWidth={5} />
                        <Line isAnimationActive={false} dot={false} type="monotone" dataKey="power" yAxisId="right" stroke="#ff0000" />
                    </ComposedChart>
                </ResponsiveContainer>

                    </div>
            {/* <img src={demo.src} alt="demo" className="w-screen h-screen absolute" /> */}

        </motion.div>
    );
};

export default MissionControl;

