"use client";

import { Button } from "@buildo/bento-design-system";
import { BiSolidCoffeeAlt } from "react-icons/bi";
import { IoIosSettings } from "react-icons/io";

import useDeviceContext from "@/hooks/useDeviceContext";
import { useSelectedLanguage, useTranslation } from "next-export-i18n";
import { useRouter } from "next/router";
import { MdLanguage } from "react-icons/md";
import { cn } from "@/util";



const LanguagePicker = () => {
  const { switchLocales } = useDeviceContext();
  const { lang } = useSelectedLanguage();
  return (
    <div
      className="absolute bottom-0 right-0 m-1 flex cursor-pointer flex-row items-center space-x-2 rounded-xl border p-4 hover:text-purple-900 select-none"
      onClick={() => {
        const current = localStorage.getItem("next-export-i18n-lang");

        if (current == "en") {
          console.log('switch language to vi')
          switchLocales("vi");
        } else {
          console.log('switch language to en')

          switchLocales("en");
        }
      }}
    >
      <MdLanguage size={16} />
      {/* <h3 className="">{lang == "en" ? "Ting Vit" : "English"}</h3> */}
    </div>
  );
};

const BottomNavigationBar = () => {
  const router = useRouter();
  const { selectedPage, setSelectedPage } = useDeviceContext();

  const { t, locales } = useTranslation();

  return (
    <div
      className="fixed bottom-0 z-50 flex h-14 w-full justify-center bg-white"
      style={{
        boxShadow: "1px -3px 5px 0px rgba(0,0,0,0.19)",
      }}
    >
      <LanguagePicker />
      <div>
        <div className="flex flex-row  items-center justify-between">
          {router.pathname == "/lab" ? (
            <div
              className="cursor-pointer flex-row items-center space-x-2 rounded-xl p-4"
              onClick={() => {
                // setSelectedPage("Settings");
                if (router.pathname == "/lab") {
                  router.push("/");
                }
              }}
            >
              <h1 className="text-xl font-bold dark:text-white">
                {router.pathname == "/lab" ? t(" Back") : selectedPage}
              </h1>
            </div>
          ) : (
            <div className="flex p cursor-pointer items-center space-x-8   p-4">
                <button className={cn(
                  "flex items-center space-x-2 ",
                  selectedPage === "Formula" ? "text-black": "text-gray-300"
                )}
                  onClick={() => {
                    setSelectedPage("Formula");
                    router.push("/");

                  }}
                >
                  <BiSolidCoffeeAlt />
                  <h1>{t("Formula")}</h1>

                </button>
                
                <button className={cn(
                  "flex items-center space-x-2 ",
                  selectedPage === "Setting" ? "text-black": "text-gray-300"
                )}
                  onClick={() => {
                    setSelectedPage("Setting");
                    router.push("/setting");

                  }}
                >
                  <IoIosSettings />
                  <h1>{t("Setting")}</h1>

              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};
export default BottomNavigationBar;
