export enum EnumFormulaType {
  Espresso = "Espresso",
  Teapresso = "Teapresso",
  PourOver = "PourOver",
}

export class Formula {
  steps: Step[];
  name?: string;
  uuid?: number;
  createdAt?: number;
  lastUsed?: number;
  totalUsed?: number;

  constructor({
    steps,
    name,
    uuid,
    createdAt,
    lastUsed,
    totalUsed,
  }: {
    steps: Step[];
    name?: string;
    uuid?: number;
    createdAt?: number;
    lastUsed?: number;
    totalUsed?: number;
  }) {
    this.steps = steps;
    this.name = name;
    this.uuid = uuid;
    this.createdAt = createdAt;
    this.lastUsed = lastUsed;
    this.totalUsed = totalUsed;
  }
  toData() {
    const steps: Step[] = [];
    this.steps.forEach((step) => {
      steps.push(step);
    });
    return {
      steps: steps,
      name: this.name,
      uuid: this.uuid,
      createdAt: this.createdAt,
      lastUsed: this.lastUsed,
      totalUsed: this.totalUsed,
    };
  }

  fromData(data: any) {
    const steps = data.steps.map((step: any) => Step.fromData(step));
    return new Formula({
      steps: steps,
      name: data.name,
      uuid: data.uuid,
      createdAt: data.createdAt,
      lastUsed: data.lastUsed,
      totalUsed: data.totalUsed,
    });
  }
}

