"use client";

import { queryDevice } from "./connection";
import { initialState } from "./provider";

export interface IGrinderState {
  connectionType: "Wire" | "Bluetooth";
  connectionId: number;
  calibration: number;
  id: number;
  name: string;
}

export enum EnumSkipBehaviour {
  Skip = "Skip",
  Wait = "Wait",
}

export const grinderInitialState: IGrinderState[] = [
  {
    connectionType: "Wire",
    connectionId: 20,
    calibration: 1.17,
    id: 0,
    name: "Arabica",
  },
  {
    connectionType: "Wire",
    connectionId: 20,
    calibration: 1.17,
    id: 0,
    name: "Coco",
  },
];
export class GrinderSettingParameter {
  connectionType: "Wire" | "Bluetooth" = "Wire";
  connectionId: number = 0;
  calibration: number = 0;
  constructor({ connectionType, connectionId, calibration }: IGrinderState) {
    this.connectionType = connectionType;
    this.connectionId = connectionId;
    this.calibration = calibration;
  }
}

export async function setGrinderConfiguration() {
  // map each of the pid state to this
  const ctx = (window as any).ctx as typeof initialState;
  const grinders = ctx.grinderSettings;

  await queryDevice({
    command: "grinder.set",
    grinders,
  });
}

export async function getGrinderConfiguration() {
  const req = await queryDevice({
    command: "grinder.get",
  });
  const grinderSettings = req["response"]["grinderSettings"];
  console.log("grinder/ acquired", grinderSettings);

  const ctx = (window as any).ctx as typeof initialState;
  // ctx.grinderSettings.forEach(draft => {
  //     grinderSettings.set
  // })
  ctx.setGrinderSettings(grinderSettings);
}
