export enum ProfileType {
    PressureProfiling = "PressureProfile",
    FlowrateProfiling = "FlowateProfile",
};
export enum TargetType {
    Weight = "Weight",
    Time = "Time",
};
export class Time {
    duration: number;
    constructor({ duration }: { duration: number }) {
        this.duration = duration;
    }
    toData() {
        return {
            targetType: TargetType.Time,
            duration: this.duration,
        }
    }
    static fromData(data: any) {
        return new Time({
            duration: data.duration
        });
    }
}
export class Weight {
    weight: number;
    constructor({ weight }: { weight: number }) {
        this.weight = weight;
    }
    toData() {
        return {
            targetType: TargetType.Weight,
            weight: this.weight,
        }
    }

    static fromData(data: any) {
        return new Weight({
            weight: data.weight
        });
    }
}
export type TargetClass = Time | Weight;
