"use client";

import { decode, encode } from "@msgpack/msgpack";
import { interpretMessage } from "./event";
import { getGrinderConfiguration } from "./grinder";
import { syncPidConfigurationFromDevice } from "./pid";
import { initialState } from "@/gateway/provider";
import { produce } from "immer";
import { syncCalibrationFromDevice } from "./calibration";

async function sleep(ms: number) {
  await new Promise((resolve) => setTimeout(resolve, ms));
}


export var isInitialized = false

async function initializeSync() {
  console.log('initilizeSync')
  // while the device is still connected, do the sync
  //% load the pid state
  const ctx = (window as any).ctx as typeof initialState

  for (var channel = 0; channel < 3; channel++) {
    await syncPidConfigurationFromDevice({ channel })
  }

  await syncCalibrationFromDevice()
  isInitialized = true

}

export function handleConnectButton() {
  console.debug("State", device);
  if (device === null) {
    requestDevice();
  } else {
    disconnectDevice();
  }
}

export interface Header {
  isFinished: boolean;
  isFirst: boolean;
  isCompressed: boolean;
  requireAck: boolean;
  channel: number;
  payloadLength: number;
  frameType: number;
}

export let device: BluetoothDevice | null = null;
export let charact: BluetoothRemoteGATTCharacteristic;
export let mtu: number = 20;
export let incomeBuffer: Uint8Array = new Uint8Array();
export let reconnectTimeout: ReturnType<typeof setTimeout>;

export async function connectDevice(device: BluetoothDevice) {
  console.debug("gateway/ attempt to connect to device");
  if (device?.gatt == undefined) {
    console.error("ble/ gatt doesn't exist", device);
    return;
  }

  await device.gatt.connect();
  const rosarioService = await device.gatt.getPrimaryService(
    "0000ffe0-0000-1000-8000-00805f9b34fb",
  );
  console.debug("ble/ rosarioService connected", rosarioService);
  charact = await rosarioService.getCharacteristic(
    "0000ee00-0000-1000-8000-00805f9b34fb",
  );
  charact.addEventListener("characteristicvaluechanged", (event: Event) => {
    const recv = (event.target as BluetoothRemoteGATTCharacteristic).value;
    if (recv == null) return;
    decodeMessage(new Uint8Array(recv.buffer));
  });
  charact.startNotifications();
  console.debug("ble/ general characteristic connected", charact);

  // read the mtu characteristic
  const mtuCharact = await rosarioService.getCharacteristic(
    "0000ee01-0000-1000-8000-00805f9b34fb",
  );

  // Convert DataView to int, LSB
  mtu = (await mtuCharact.readValue()).getUint16(0, true);
  if (mtu >= 500) {
    mtu = 500;
  }

  const firmwareCharact = await rosarioService.getCharacteristic(
    "0000ee02-0000-1000-8000-00805f9b34fb",
  );

  // Convert DataView to int, LSB
  let firmwareVersion = 0;
  firmwareVersion = (await firmwareCharact.readValue()).getUint16(0, true);

  console.debug(
    "Connect device success",
    device.name,
    "fw:",
    firmwareVersion,
    "mtu:",
    mtu,
  );

  (window as any).ctx.setConnectButtonStyle("solid");
  (window as any).ctx.setConnectButtonText(
    device.name?.replace("AnataLAB", "") || "Device",
  );
  (window as any).ctx.setConnected(true);

  setTimeout(async () => {
    await initializeSync();
  }, 2000);
}

export function decodeMessage(data: Uint8Array) {
  console.debug("/");
  // Get the first 4 bytes of data
  const headerRaw = data.slice(0, 4);
  const header = {
    isFinished: headerRaw[0] & 0b00000001 ? true : false,
    isFirst: headerRaw[0] & 0b00000010 ? true : false,
    isCompressed: headerRaw[0] & 0b00000100 ? true : false,
    requireAck: headerRaw[0] & 0b00001000 ? true : false,
    channel: headerRaw[1],
    payloadLength: headerRaw[3] + (headerRaw[2] << 8),
    frameType: headerRaw[0] & 0b11110000, // this is reversed
  };
  const payload = data.slice(4);
  console.debug("decodeMessage", header, header.frameType);

  if (header.frameType == 48) {
    console.debug("ack signal received");
    mailbox[header.channel] = true;
    return;
  }

  if (header.isFirst) {
    incomeBuffer = new Uint8Array();
  }
  const currentBuffer = incomeBuffer;
  incomeBuffer = new Uint8Array(currentBuffer.length + payload.length);
  incomeBuffer.set(currentBuffer);
  incomeBuffer.set(payload, currentBuffer.length);

  if (header.isFinished) {
    if (header.isCompressed) {
      throw Error("not supported yet");
    }
    /** @type {object} */

    try {
      const data = decode(incomeBuffer);
      // console.debug("HandleIncome", data, mailbox)
      interpretMessage(header as Header, data as number[]);
    } catch (e) {
      console.warn("error decode", e, incomeBuffer);
    } finally {
      incomeBuffer = new Uint8Array();
    }
  }
}
export async function requestDevice() {
  console.debug("gateway/ device requested");
  if (device != null) return;

  device = await window.navigator.bluetooth.requestDevice({
    filters: [{ namePrefix: "AnataLAB" }],
    optionalServices: [
      "0000ffe1-0000-1000-8000-00805f9b34fb",
      "0000ffe0-0000-1000-8000-00805f9b34fb",
    ],
  });
  if (!device) return;
  device.addEventListener("gattserverdisconnected", () => {

  

    location.reload()

    isInitialized = false
    console.debug(`ble/ disconnected`, "Red");
    reconnectTimeout = setTimeout(async () => {
      if (device) {
        await connectDevice(device);
      }
    }, 10000);
    (window as any).ctx.setConnectButtonStyle("outline");
    (window as any).ctx.setConnectButtonText("Retrying ...");
    (window as any).ctx.setConnected(false);
  });
  await connectDevice(device);
}
export async function disconnectDevice() {
  console.debug("gateway/ device disconnected");
  if (device == null) return;
  clearTimeout(reconnectTimeout);
  device.gatt?.disconnect();
  device = null;

  console.debug("CTX", (window as any).ctx);
  (window as any).ctx.setConnectButtonStyle("outline");
  (window as any).ctx.setConnectButtonText("Connect");
  (window as any).ctx.setConnected(false);
}

const splitChunk = (arr: Uint8Array, size: number) =>
  Array.from({ length: Math.ceil(arr.length / size) }, (v, i) =>
    arr.slice(i * size, i * size + size),
  );

const encodeMessage = (chunk: Uint8Array, header: Header) => {
  // generate the 4 header flags, then combine it with the chunk
  var headerByte = 0x00;
  if (header.isFinished) {
    headerByte = headerByte | 0b00000001;
  }
  if (header.isFirst) {
    headerByte = headerByte | 0b00000010;
  }
  if (header.isCompressed) {
    headerByte = headerByte | 0b00000100;
  }
  if (header.requireAck) {
    headerByte = headerByte | 0b00001000;
  }

  const headers = new Uint8Array([
    headerByte,
    header.channel,
    header.payloadLength / 256,
    header.payloadLength % 256,
  ]);

  const finalized = new Uint8Array(headers.length + chunk.length);
  finalized.set(headers);
  finalized.set(chunk, headers.length);
  return finalized;
};

function assignRandomId(): number {
  (window as any).randomId = (window as any).randomId || 1;
  (window as any).randomId += 1;
  if ((window as any).randomId > 200) {
    (window as any).randomId = 1;
  }
  return (window as any).randomId;
}

export let mailbox = {};

let isSending = false;
export async function queryDevice(params: any) {
  if (!(window as any).ctx.connected) return;
  while (isSending) {
    await sleep(10);
  }
  isSending = true;
  /*
        main point of contact between api and device
        encode params, split to chunk, add the header and send
        wait for mailbox for return
        remember to set ack flag
    */
  try {
    params.id = new Date().getTime();
    const encoded = encode(params);
    const chunks = splitChunk(encoded, mtu - 3);
    console.debug(`splited into ${chunks.length} chunks`);

    mailbox[params.id] = null;

    for (var idx = 0; idx < chunks.length; idx++) {
      const chunk = chunks[idx];
      const selectedChannl = assignRandomId();
      const signed = encodeMessage(chunk, {
        isFinished: idx == chunks.length - 1,
        isFirst: idx == 0,
        isCompressed: false,
        requireAck: true,
        channel: selectedChannl,
        payloadLength: chunk.length,
        frameType: 0,
      });
      mailbox[selectedChannl] = null;
      await charact.writeValue(signed);
      let startTs = new Date().getTime();
      while (new Date().getTime() - startTs < 3000) {
        if (mailbox[selectedChannl] != null) {
          break;
        }
        await sleep(1);
      }
      if (mailbox[selectedChannl] == null) {
        throw new Error("no ack signal received !");
      }
    }

    // now we wait for response
    let futureTs = new Date().getTime() + 5000;
    while (mailbox[params.id] == null) {
      await sleep(1);
      if (new Date().getTime() > futureTs) {
        throw new Error("no response received !");
      }
    }

    let response = mailbox[params.id];
    delete mailbox[params.id];

    console.debug("queryDevice", { params, response });

    return response;
  } catch (e) {
    return null;
  } finally {
    isSending = false;
  }
}
