export const DeviceContext = createContext(initialState);

export type IFeedbackReport = {
  // this is what device send to ui
};
export function handleFeedback(data: any) {
  console.log("Report/", JSON.stringify(data, null, 2));
  const ctx = (window as any).ctx as typeof initialState;

  const { formulaId, stepId, target, action } = data as {
    formulaId: string;
    stepId: number;
    target: {
      type: EnumTarget;
      total: number;
      value: number;
    };
    action: {
      profilingType: string;
      expectedValue: number;
      timestamp: number;
      input: number;
      output: number;
    };
  };
  // const { type, total, value } = target;

  if (data.command == "rp.start" && data.isLocal == true) {
    ctx.setModalDisplay(true)
    try {
      (window as any).startMissionControl(data)

    }
    catch (e) {
      console.error(e)
    }

  }
  else if (data.command == "rp.finish") {
    (window as any).finishMissionControl()
    // setTimeout(() => {
    //   ctx.setModalDisplay(false)

    // }, 10000)
  }
  else if (data.command == "rp.progress") {
    // update the progress locally
    if (ctx.modalDisplay) {
      // stream to mission control, don't set any feedbacks
      
      try {
        (window as any).updateMissionControl(data)

      }
      catch (e) {
        
      }
      return;
    }
  }


  ctx.setformulaUpdatedTimestamp(produce(ctx.formulaUpdatedTimestamp, (draft) => {
    draft[data.formulaId] = new Date().getTime()
    return draft
  }))

  ctx.setFeedbacks(
    produce(ctx.formulaFeedbacks, (draft) => {
      // draft[formulaId][stepId].target = target
      const formulaUpdate: Record<number, IStepFeedback> = draft[formulaId] || {};
      draft[formulaId] = formulaUpdate;

      // mofify the step of the formula update
      formulaUpdate[stepId] = formulaUpdate[stepId] || ({} as IStepFeedback);
      formulaUpdate[stepId].target = target;
      formulaUpdate[stepId].action = action as IStepFeedback["action"];


      return draft;
    }),
  );
  console.log("Update/ : Set formulaUpdatedTimestamp to ", data.formulaId)
}

export type DisplayFormulaParam = {
  id: string;
  title: string;
  text: string;
  color: string;
  backdrop: string;
  formulaType: EnumFormulaType;
  steps: IStep[];
  buildupDuration: number;
  lossyWeight: number; // amount of water that is lost, convertSteps will add this to the last step
  maximumDuration: number;
  isShared: boolean;
  category: string;
};

export function DeviceProvider({ children }) {
  const { t } = useTranslation();
  const [connectButtonStyle, setConnectButtonStyle] = useState(
    initialState.connectButtonStyle,
  );
  const [connectButtonText, setConnectButtonText] = useState(t("Connect"));
  const [connected, setConnected] = useState(initialState.connected);
  const [selectedFormulaId, registerSelectedFormulaId] =
    useLocalStorage<string>("rapp-selectedFormulaId", "");

  const { lang } = useSelectedLanguage();
  const [calibration, setCalibration] = useState(initialState.calibration)
  const [formulaUpdatedTimestamp, setformulaUpdatedTimestamp] = useState(initialState.formulaUpdatedTimestamp)

  useEffect(() => {
    if (lang == "en" && !connected) {
      setConnectButtonText("Connect");
    } else if (lang == "vi" && !connected) {
      setConnectButtonText("Kt ni");
    }
  }, [lang, connected]);

  const [formulas, setFormulas] = useLocalStorage<
    Record<string, DisplayFormulaParam>
  >("rapp-fml", {});

  const [formulaFeedbacks, setFeedbacks] = useState<
    Record<string, Record<number, IStepFeedback>>
  >({});

  const [layout, setLayout] = useLocalStorage<Layouts>("rapp-layout", {});
  // const { toast } = usseToast();
  //   (window as any).toast = toast

  const router = useRouter();
  const pathMapping = {
    "/": "Formula",
    "/lab": "Alchemy",
    "/setting": "Setting",
  };
  const initialSelectedPage = pathMapping[router.pathname];
  console.log(`map/ set initialSelectedPage to ${initialSelectedPage}`);
  const [selectedPage, setSelectedPage] = useState(initialSelectedPage);
  const [pidState, setPidState] = useState<IPidState[]>(initialState.pidState);
  const [pidDisplay, setPidDisplay] = useState<IPidDisplay[]>(
    initialState.pidDisplay,
  );
  const [grinderSettings, setGrinderSettings] = useState<IGrinderState[]>(
    initialState.grinderSettings,
  );
  const [darkMode, setDarkMode] = useLocalStorage<boolean>("darkMode", false);
  const [language, setLanguage] = useLocalStorage<string>("language", "en");
  const [draggable, setDraggable] = useState(
    Object.entries(formulas).length == 0,
  );
  // const [steps, setSteps] = useState<IStep[]>([]);
  const [style, setStyle] = useState(initialState.style);
  const [flowCount, setFlowCount] = useState(initialState.flowCount);
  const [flowRate, setFlowRate] = useState(initialState.flowRate);
    const [pressure, setPressure] = useState(initialState.pressure)
  const [flowmeterDisplay, setFlowmeterDisplay] = useState(initialState.flowmeterDisplay)

  const [modalDisplay, setModalDisplay] = useState<boolean>(false);

  const [addFormulaOpen, setAddFormulaOpen] = useState(false);
  useEffect(() => {
    if (darkMode) {
      document.body.classList.add("dark");
    } else {
      document.body.classList.remove("dark");
    }
  }, [darkMode]);

  const [progressReport, setProgressReport] = useState<
    Record<string, IProgressReport>
  >({});

  // useEffect(() => {
  //   manageSync({ ctx, connected });
  // }, [connected]);

  const value = useMemo(
    () => ({
      connectButtonStyle,
      connectButtonText,
      selectedPage,
      pidState,
      pidDisplay,
      connected,
      grinderSettings,
      selectedFormulaId,
      formulas,
      layout,
      calibration,
      // steps,
      darkMode,
      language,
      draggable,
      style,
      addFormulaOpen,
      progressReport,
      formulaFeedbacks,
      flowmeterDisplay,
      setformulaUpdatedTimestamp,
      formulaUpdatedTimestamp,
      modalDisplay,
      setModalDisplay,
      pressure, 
      setPressure

    }),
    [
      grinderSettings,
      connected,
      connectButtonStyle,
      connectButtonText,
      selectedPage,
      pidState,
      pidDisplay,
      selectedFormulaId,
      formulas,
      layout,
      calibration,
      // steps,
      draggable,
      language,
      darkMode,
      style,
      addFormulaOpen,
      progressReport,
      formulaFeedbacks,
      flowmeterDisplay,
      setformulaUpdatedTimestamp,
      formulaUpdatedTimestamp,
      setModalDisplay,
      modalDisplay,
      pressure, 
      setPressure

    ],
  );

  const setValue = useMemo(
    () => ({
      setConnected,
      setConnectButtonStyle,
      setConnectButtonText,
      setSelectedPage,
      setPidState,
      setPidDisplay,
      setGrinderSettings,
      registerFormulaClick,
      cancelFormulaClick,
      downloadFormula,
      registerSelectedFormulaId,
      setLayout,
      setFormulas,
      setCalibration,
      // setSteps,
      setDarkMode,
      setLanguage,
      setDraggable,
      setStyle,
      setAddFormulaOpen,
      setProgressReport,
      setFeedbacks,
      switchLocales,
      setFlowCount,
      setFlowRate,
      setFlowmeterDisplay,
    }),
    [
      setConnected,
      setConnectButtonStyle,
      setConnectButtonText,
      setSelectedPage,
      setPidState,
      setPidDisplay,
      setGrinderSettings,
      registerSelectedFormulaId,
      setLayout,
      setFormulas,
      // setSteps,
      setDarkMode,
      setLanguage,
      setDraggable,
      setStyle,
      setAddFormulaOpen,
      setProgressReport,
      setFeedbacks,
      setFlowCount,
      setFlowRate,
      setFlowmeterDisplay,
    ],
  );

  const props = useMemo(() => ({ ...value, ...setValue }), [setValue, value]);

  useEffect(() => {
    (window as any).ctx = props;
  }, [props]);

  useEffect(() => {
    updateFlag.pidState = true; // mark that pid is updated, so a sync will be pending
  }, [pidState, setPidState]);

  useEffect(() => {
    updateFlag.calibrationState = true
  }, [calibration])

  useEffect(() => {
    updateFlag.grinderState = true;
  }, [grinderSettings, setGrinderSettings]);

  useEffectOnce(() => {
    // dont use useEffect, it will create 3 intervals
    var win = (window as any)

    if (win.syncInterval) clearInterval(win.syncInterval);
    win.syncInterval = setInterval(async () => await manageSync(), 5000);


    // create a private key


    if (win.pullInterval) clearInterval(win.pullInterval);
    win.pullInterval = setInterval(async () => await managePull(), 10000);

    setTimeout(async () => {
      await registerServiceWorker();
    }, 1000);

    window.addEventListener('keydown', (evt) => {
      if (evt.ctrlKey) {
        if (evt.key == 'k') {
           setModalDisplay(true)
        }
       }
     });


  });

  // Todo: fix later, too lzay
  const contextValue = {
    ...props,
    flowCount: [],
    flowRate: [],
  };

  return (
    <DeviceContext.Provider value={contextValue}>{children}</DeviceContext.Provider>
  );
}
export default DeviceContext;
