import { ProfileType } from "./target";

export enum ActionType {
  Grinding = "Grinding",
  Extracting = "Extracting",
  Profiling = "Profilling",
}
export enum ConnectionType {
  Wire = "Wired",
  Bluetooth = "Bluetooth",
}

export class GrinderAction {
  // grinder have grinderId, grinderConnection
  grinderId: number;
  grinderConnection: ConnectionType;
  canSkip: boolean;
  canWait: boolean;

  constructor({
    grinderId,
    grinderConnection,
    canSkip,
    canWait,
  }: {
    grinderId: number;
    grinderConnection: ConnectionType;
    canSkip: boolean;
    canWait: boolean;
  }) {
    this.grinderId = grinderId;
    this.grinderConnection = grinderConnection;
    this.canSkip = canSkip;
    this.canWait = canWait;
  }

  toData() {
    return {
      actionType: ActionType.Grinding,
      grinderId: this.grinderId,
      grinderConnection: this.grinderConnection,
      canSkip: this.canSkip,
      canWait: this.canWait,
    };
  }

  static fromData(data: any) {
    return new GrinderAction({
      grinderId: data.grinderId,
      grinderConnection: data.grinderConnection,
      canSkip: data.canSkip,
      canWait: data.canWait,
    });
  }
}
export class PumpAction {
  power: number;
  valveState: boolean;
  constructor({ power, valveState }: { power: number; valveState: boolean }) {
    this.power = power;
    this.valveState = valveState;
  }
  toData() {
    return {
      actionType: ActionType.Extracting,
      power: this.power,
      valveState: this.valveState,
    };
  }

  static fromData(data: any) {
    return new PumpAction({
      power: data.power,
      valveState: data.valveState,
    });
  }
}
export class ProfilingAction {
  profileType: ProfileType;
  segments: Segment[];
  refreshInterval: number; // how fast we update
  kp: number;
  ki: number;
  kd: number;

  constructor({
    profileType,
    segments,
    refreshInterval,
  }: {
    profileType: ProfileType;
    segments: Segment[];
    refreshInterval: number;
  }) {
    this.profileType = profileType;
    this.segments = segments;
    this.refreshInterval = refreshInterval;
  }
  toData() {
    const steps: Segment[] = [];
    this.segments.forEach((segment) => {
      steps.push(segment);
    });

    return {
      actionType: ActionType.Profiling,
      profileType: this.profileType,
      refreshInterval: this.refreshInterval,
      segments: steps,
    };
  }
  static fromData(data: any) {
    const segments = data.segments.map((segment: any) =>
      Segment.fromData(segment),
    );
    return new ProfilingAction({
      profileType: data.profileType,
      segments: segments,
      refreshInterval: data.refreshInterval,
    });
  }
}
export class Segment {
  duration: number;
  value: number;

  constructor({ duration, value }: { duration: number; value: number }) {
    this.duration = duration;
    this.value = value;
  }

  toData() {
    return {
      duration: this.duration,
      value: this.value,
    };
  }

  static fromData(data: any) {
    return new Segment({
      duration: data.duration,
      value: data.value,
    });
  }
}
export type ActionClass = GrinderAction | PumpAction | ProfilingAction;
