import {initialState as providerInitialState} from "@/gateway/provider";
import { queryDevice } from "./connection";
import {produce} from 'immer'

const initialState = {
    flowmeter: [1, 1, 1],
    grinder: [1, 1, 1],
    pid: [0,0,0]
}

const calibration = {
    initialState,
}
export default calibration;

export async function syncCalibrationFromDevice() {
    const request = await queryDevice({
        command: 'calib.get',
    });

    // return {
    //     flowmeter: request['response']['calibration']['flowmeter'] as number[],
    //     grinder: request['response']['calibration']['grinder'] as number[],
    //     temperature: request['response']['calibration']['temperature'] as number[],
    // };

    const ctx = (window as any).ctx as typeof providerInitialState
    ctx.setCalibration(produce(ctx.calibration, draft => {
        draft.flowmeter = request['response']['calibration']['flowmeter'] as number[]
        draft.grinder = request['response']['calibration']['grinder'] as number[]
        // draft.temperature = request['response']['calibration']['temperature'] as number[]
        console.log('calibration/ pulled from device', request['response']['calibration'])
        return draft
    })
    )

    // !

    // ctx.calibration = {
    //     flowmeter: request['response']['calibration']['flowmeter'] as number[],
    //     grinder: request['response']['calibration']['grinder'] as number[],
    //     // temperature: request['response']['calibration']['temperature'] as number[],
    // }

}

import { isInitialized } from './connection'


export async function setCalibration({ flowmeter, grinder, temperature }: { flowmeter: number[], grinder: number[], temperature: number[] }) {
    if (!isInitialized) return 



    return await queryDevice({
        command: 'calib.set',
        calibration: {
            flowmeter,
            grinder,
            temperature
        }
    });
}


