"use client";

import { produce } from "immer";
import { queryDevice } from "./connection";
import { initialState } from "./provider";
import { isInitialized } from "./connection";

// + finalized
export interface IPidState {
  isActive: boolean;
  targetTemperature: number;
  proportional: number;
  integral: number;
  derivative: number;
  activeRange: number;
}

export const pidInitialState: IPidState[] = new Array(3).fill({
  isActive: false,
  proportional: 0,
  integral: 0,
  derivative: 0,
  targetTemperature: 0,
  activeRange: 0,
});

export interface IPidDisplay {
  currentTemperature: number;
  currentPower: number;
  powerSamples: number[];
  temperatureSamples: number[];
}
export const pidInitialDisplay: IPidDisplay[] = new Array(3).fill({
  currentTemperature: 0,
  currentPower: 0,
  temperatureSamples: Array(100).fill(0),
  powerSamples: Array(100).fill(0),
});

export class PidSettingParameter {
  proportional: number;
  integral: number;
  derivative: number;
  targetTemperature: number;
  activeRange: number;
  isActive: boolean;

  constructor({
    proportional,
    integral,
    derivative,
    targetTemperature,
    activeRange,
    isActive,
  }: {
    proportional: number;
    integral: number;
    derivative: number;
    targetTemperature: number;
    activeRange: number;
    isActive: boolean;
  }) {
    this.proportional = proportional;
    this.integral = integral;
    this.derivative = derivative;
    this.targetTemperature = targetTemperature;
    this.activeRange = activeRange;
    this.isActive = isActive;
  }
  isEqual(other: PidSettingParameter): boolean {
    return (
      this.proportional === other.proportional &&
      this.integral === other.integral &&
      this.derivative === other.derivative &&
      this.targetTemperature === other.targetTemperature &&
      this.activeRange === other.activeRange &&
      this.isActive === other.isActive
    );
  }
}

export class PidStatusParameter {
  currentTemperature: number;
  currentPower: number;
  temperatureSamples: number[];
  powerSamples: number[];
  constructor({
    currentTemperature,
    currentPower,
    temperatureSamples,
    powerSamples,
  }: {
    currentTemperature: number;
    currentPower: number;
    temperatureSamples: number[];
    powerSamples: number[];
  }) {
    this.currentTemperature = currentTemperature;
    this.currentPower = currentPower;
    this.temperatureSamples = temperatureSamples;
    this.powerSamples = powerSamples;
  }
}

const syncedPidState = new Map<number, PidSettingParameter>();
export async function setPidConfiguration({
  channel,
  pid,
}: {
  channel: number;
  pid: PidSettingParameter;
}) {
  // only send if different

  // dont run this if device hasn't get the initializeSync process done
  if (!isInitialized) {
    return false;
  }
  await queryDevice({
    command: "pid.set",
    channel: channel,
    proportional: pid.proportional,
    integral: pid.integral,
    derivative: pid.derivative,
    targetTemperature: pid.targetTemperature,
    activeRange: pid.activeRange,
    isActive: pid.isActive,
  });
  return true;
}

export async function syncPidConfigurationFromDevice({
  channel,
}: {
  channel: number;
}): Promise<PidSettingParameter> {
  const request = await queryDevice({
    command: "pid.get",
    channel,
  });

  const pid = new PidSettingParameter({
    proportional: request["response"]["proportional"] as number,
    integral: request["response"]["integral"] as number,
    derivative: request["response"]["derivative"] as number,
    targetTemperature: request["response"]["targetTemperature"] as number,
    activeRange: request["response"]["activeRange"] as number,
    isActive: request["response"]["isActive"] as boolean,
    // history: {
    //     power: request['response']['history']['power'] as number[],
    //     temperature: request['response']['history']['temperature'] as number[]
    // }
  });

  const ctx = (window as any).ctx as typeof initialState;
  ctx.setPidState(
    produce(ctx.pidState, (draft) => {
      draft[channel].proportional = pid.proportional;
      draft[channel].integral = pid.integral;
      draft[channel].derivative = pid.derivative;
      draft[channel].targetTemperature = pid.targetTemperature;
      draft[channel].activeRange = pid.activeRange;
      draft[channel].isActive = pid.isActive;
    }),
  );

  return pid;
}

export async function syncPidStatus({
  channel,
}: {
  channel: number;
}): Promise<PidStatusParameter> {
  const request = await queryDevice({
    command: "pid.status",
    channel: channel,
  });

  const status = new PidStatusParameter({
    currentTemperature: request["response"]["currentTemperature"] as number,
    currentPower: request["response"]["currentPower"] as number,
    temperatureSamples: request["response"]["temperatureSamples"] as number[],
    powerSamples: request["response"]["powerSamples"] as number[],
  });
  console.log("sync pid status", {status, channel, request  });

  const ctx = (window as any).ctx as typeof initialState;
  

  ctx.setPidDisplay(produce(ctx.pidDisplay, draft => {
      draft[channel].currentTemperature = status.currentTemperature;
      draft[channel].currentPower = status.currentPower;
      draft[channel].temperatureSamples = status.temperatureSamples;
      draft[channel].powerSamples = status.powerSamples;
  }))

  return status;
}
