const TopNavigationBar = () => {
  const {
    connectButtonStyle,
    connectButtonText,
    selectedPage,
    draggable,
    setDraggable,
    darkMode,
    setDarkMode,
    language,
    setLanguage,
    connected,
    setAddFormulaOpen,
  } = useDeviceContext();
  const router = useRouter();

  const [isOpen, setIsOpen] = useState(false);
  const { t } = useTranslation()
  const {
    formulas, setFormulas, layout, setLayout
  } = useDeviceContext()

  async function addRandomFormula() {
    const formulaId = uuid()
    const randomColors = ["#f44336", "#e91e63", "#9c27b0", "#673ab7", "#3f51b5", "#2196f3", "#03a9f4", "#00bcd4", "#009688", "#4caf50", "#8bc34a", "#cddc39", "#ffeb3b", "#ffc107", "#ff9800", "#ff5722", "#795548", "#607d8b"]
    const color = randomColors[Math.floor(Math.random() * randomColors.length)]


    setLayout (
      produce(layout, draft => {
        draft["md"]?.push({ i: formulaId, x: 0, y: 0, w: 1, h: 1 });
        draft["xs"]?.push({ i: formulaId, x: 0, y: 0, w: 1, h: 1 });
        draft["sm"]?.push({ i: formulaId, x: 0, y: 0, w: 1, h: 1 });

      })
    )

    setFormulas(
      produce(formulas, draft => {
        const formula = {
          id: formulaId,
          title: "",
          text: "",
          color: color,
          backdrop: "gray", // deprecated
          formulaType: EnumFormulaType.Espresso,
          steps: [],
          buildupDuration: 0,
          lossyWeight: 0,
          maximumDuration: 0,
          isShared: false,
          category: ""
        }

        draft[formulaId] = formula


      })
    )
  }



  const fns = [
    {
      key: "community",
      onClick: () => { },
      title: "From Community",
      text: "Explore the shared formula from people around the world !",
      animationData: lottieEarth,
    },

    {
      key: "token",
      onClick: () => { },
      title: "From Token",
      text: "Load from a SHARE code!",
      animationData: lottieLotus,
    },

    {
      key: "invent",
      onClick: async () => {
        // setAddFormulaOpen(true)

        // just invent by add a new shit in, let user edit inside lab view, smart

        await addRandomFormula()

      },
      title: "Let's Invent",
      text: "Create new formula now !",
      animationData: lottieCoffee,
    },

  ]


  const imageButtons = fns.map((fn) => (
    <button
      key={fn.key}
      onClick={() => {
        setIsOpen(false)
        fn.onClick()
      }}
      className="flex items-center justify-between h-20 w-full bg-transparent hover:bg-blue-500 text-black  hover:text-white py-2 px-4  hover:border-transparent rounded-xl "
    >
      {/* <img src="/path/to/image.png" alt="Icon" className="w-8 h-8" /> */}
      <Lottie
        options={{
          loop: true,
          autoplay: true,
          animationData: fn.animationData,
          rendererSettings: {
            preserveAspectRatio: 'xMidYMid slice'
          }
        }}
        height={80}
        width={80}
      />

      <span className="flex-1 text-left ml-4">
        <div className="text-lg font-bold">{fn.title}</div>
        <div className="text-sm ">{fn.text}</div>
      </span>
    </button>
  ))


  return (
    <>

      <div className="fixed z-10 flex w-full items-center justify-between bg-transparent p-2 backdrop-blur-[20px]">
        <div
          className="logo overflow-hidden "
          onClick={() => {
            router.push("/");
          }}
        >
          <Image src={AnataLABLogo} alt="AnataLAB Logo" width={80} />
        </div>

        <div className="flex h-[48px] flex-row items-center justify-evenly space-x-1">
          {router.pathname == "/" && (
            <>
              {draggable && (
                <Dialog>
                  <DialogTrigger>

                    <div
                      className={`flex h-[48px] w-[48px] cursor-pointer items-center  justify-center rounded-xl border border-dashed border-black p-1 `}
                      onClick={() => {
                        // setDraggable(!draggable);
                        setIsOpen(true)

                      }}
                    >
                      <FaPlus className="text-black" />{" "}
                    </div>

                  </DialogTrigger>
                  <DialogContent>
                    <DialogHeader>
                      <DialogDescription>
                      </DialogDescription>
                    </DialogHeader>
                    <DialogFooter>
                      <DialogClose asChild>
                        <div className="flex flex-col space-y-6">
                          {imageButtons}

                        </div>

                      </DialogClose>
                    </DialogFooter>
                  </DialogContent>
                </Dialog>
              )}
              <div
                className={`flex h-[48px] w-[48px] cursor-pointer  items-center justify-center rounded-xl border border-black p-1 ${draggable ? "bg-black" : ""
                  }`}
                onClick={() => {
                  setDraggable(!draggable);
                }}
              >
                {draggable ? (
                  <IoLockOpenOutline className="text-white" />
                ) : (
                  <IoLockClosedOutline className="text-black" />
                )}
              </div>
            </>
          )}
          <div
            className={`flex h-[48px] w-[48px] cursor-pointer  items-center justify-center rounded-xl border border-black p-1 ${darkMode ? "bg-black" : ""
              }`}
            onClick={() => {
              setDarkMode(!darkMode);
            }}
          >
            {darkMode ? (
              <MdDarkMode className="text-white" />
            ) : (
              <MdOutlineWbSunny className="text-black" />
            )}
          </div>

          <div
            // className={`border border-black  p-1 rounded-xl  h-[48px] w-fit flex justify-center items-center cursor-pointer ${
            //   darkMode ? "bg-black" : ""
            // }`}

            className={`flex h-[48px] w-fit cursor-pointer flex-row items-center justify-center space-x-2 rounded-xl border border-black p-3   ${connected ? "bg-black" : ""
              }`}
            onClick={() => {
              handleConnectButton();
            }}
          >
            {connected ? (
              <MdBluetoothConnected className="text-white" />
            ) : (
              <MdBluetooth className="text-black" />
            )}
            <h1
              className={`${connected ? "text-white" : "text-black"
                } select-none text-black`}
            >
              {connectButtonText}
            </h1>
          </div>

          {/* 
        <Button
          label={connectButtonText}
          hierarchy="primary"
          icon={() => {
            return <MdBluetooth />;
          }}
          onPress={() => {
            handleConnectButton();
          }}
          kind={connectButtonStyle as "solid" | "outline"}
        /> */}
        </div>
      </div>
    </>);
};
export default TopNavigationBar;
